.pad {
    padding: 0 0 15% 0;
}

.navbar {
    background: #0692F7;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 2;
    box-shadow: 0px 4px 10px 0px rgba(0,0,0,0.6);
    margin: 0px;
}

.navbar li {
    list-style-type: none;
}

.navBoxSpace {
    display: table;
    float: left;
    width: 100px;
}

.navBox {
    display: table;
    float: left;
    width: 300px;
    cursor: pointer;
    position: relative;
    height: 100%;
}

.active {
    background: -webkit-linear-gradient(left, orange, orange) no-repeat;
    background: linear-gradient(to right, orange, orange) no-repeat;
    background-size: 100% 3px;
    background-position-x: center;
    height: 100%;
    top: 0;
    position: absolute;
    width: 100%;
}

.link {
    vertical-align: middle;
    height: 100%;
    -webkit-transform: translate(0, 50%);
            transform: translate(0, 50%);
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-weight: lighter;
    text-align: center;
    font-size: 20px;
}

.dropMenu {
    width: 100%;
    position: absolute;
    background-color: #D7EDFE;
    box-shadow: 0px 4px 10px 0px rgba(0,0,0,0.6);
}

.outerDropLink {
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
}

.dropLink {
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-weight: lighter;
    font-size: 17px;
    padding-left: 10px;
    -webkit-transform: translate(0, 50%);
            transform: translate(0, 50%);
}

.navIconSpace {
    display: table;
    float: left;
    width: 20px;
}

.navIcon {
    position: relative;
    display: none;
    cursor: pointer;
}

.iconDropMenu {
    position: absolute;
    background-color: #D7EDFE;
    width: 300px;
    box-shadow: 0px 4px 10px 0px rgba(0,0,0,0.6);
}

.iconDropLinkBox{
    cursor: pointer;
}

body {
    position: relative;
    width: 100%;
    margin: 0;
}

.bgImg {
    width: 100%;
    height: 100vh;
    background-image: -webkit-linear-gradient(rgba(0,0, 0,0.4), rgba(0,0, 0,0.4)), url(/josite/static/media/bg.e4ff631e.jpg);
    background-image: linear-gradient(rgba(0,0, 0,0.4), rgba(0,0, 0,0.4)), url(/josite/static/media/bg.e4ff631e.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    overflow: hidden;
}

.intro {
    width: 100%;
    font-family: Verdana, Geneva, sans-serif;
    text-align: center;
    position: absolute;
    top: 50vh;
    color: white;
    font-size: 17vh;
